html{
  height:100vh;
  overflow: hidden;
}
body{
  background-color: whitesmoke;
  background-image: url(/static/media/background.89655043.png);
  min-height:100%;
}
hr{
  margin-bottom: 5px;
  margin-top: 0px;
}
.screenContainer{
  position:absolute;
  width:80%;
  height:80%;
  left:80px;
}
.mainContainer{
 position: absolute;
 width:100%;
 height:100%;
 padding:-10px;
}
.header{
  display: flex;
  align-items: center;
}
.toolBar{
  display: inline-block;
  border:5px ridge;
  background-color: rgba(185, 202, 203, 0.8);
  padding: 5px;
  border-radius: 5px;
}
#createToolBar{
  display: block;
  position: relative;
  top: 0px;
  left:0px;
  width:55px;
}
#selectionToolBar{
  display: block;
  position: relative;
  width:55px;
}
.snapToolBar{
  position: fixed;
  bottom: 0px;
}
.propertiesBar{
  position: absolute;
  top: 0px;
  right: 10px;

  font-size: smaller; 
}
.propertyFieldIncorrect{
  border: red;
}
.propertyField{
  width:120px;
}
.propertyMultField{
  width:60px;
}
.propertyGroup{
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  padding: 5px;
}

.langToggleBar{
  display: flex;
  flex-direction: row;
  align-items: stretch;
  z-index:110;
}
.langToggleButtonDown{
  display: inline-block;
  border: 2px inset;
  cursor: pointer;
  width:35px;
  height:19px;
}

.langToggleButtonUp{
  display: inline-block;
  border: 2px outset;
  cursor: pointer;
  box-shadow: 0 0 0 60px rgba(0,0,0,0) inset, .1em .1em .2em #888;
  width:35px;
  height:19px;
}
