.toolBarHeader{
  display: flex;
  justify-content: center;
  font-weight: bold;
  font-size: small;
}
#canvas{
  position: absolute;
  border:5px ridge;
  background-color: rgba(185, 202, 203, 0.8);
  border-radius: 5px;
