}

.helpSectionHidden{
  display:none;
  width:0px;
  height:0px;
  z-index: 103;
}
.helpSectionShow{
  display:block;
  position: relative;
  z-index: 103;
  width:0px;
  height:0px;
  overflow-x: scroll;
}

.helpContainer{
  position:absolute;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  width:100%;
  height:100%;
  padding:"50px";
  z-index: 100;
}
.modalContainer{
  position:absolute;
  top:0px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width:100%;
  height:100%;
  padding:"50px";
  z-index: 100;
}
.loginForm{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: stretch;
}
.errorMessage{
  color:red;
}
.helpHotKey{
  font-weight: bold;
  
}
.user{
  font-weight: bold;
  position: absolute;
  right:10px;
}
.link{
  padding: 0px 5px;
}

@-webkit-keyframes rotation{
  from{-webkit-transform: rotate(0deg);transform: rotate(0deg);}
  to{-webkit-transform:rotate(360deg);transform:rotate(360deg);}
}

@keyframes rotation{
  from{-webkit-transform: rotate(0deg);transform: rotate(0deg);}
  to{-webkit-transform:rotate(360deg);transform:rotate(360deg);}
}
.spinner{
  width:50px;
  height:50px;
  background-image: url(/static/media/spinner.9da0ff5b.svg);
  background-size: cover;
  -webkit-animation: rotation infinite 1s linear;
          animation: rotation infinite 1s linear;
}
.flexCenter{
  display: flex;
  flex-direction: row;
  justify-content: center;
}
.noselect {
  -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */ /* Konqueror HTML */
       -moz-user-select: none; /* Firefox */
        -ms-user-select: none; /* Internet Explorer/Edge */
            user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome and Opera */
}

.toolButtonGroup{
    display: block;
    height: 44px;
    width: 44px;
    overflow: hidden;
    
}

.toolButtonGroupExpanded{
    position: relative;
    width:130px;
}
.toolButtonGroupCollapsed{
    position: relative;
    height: 43px;
    overflow: hidden;

}
.toolButtonGroupCollapsed > *{
    position: absolute;
    top: 0px;
    left: 0px;
}
.toolButtonGroupExpanded > *{
    position: relative;
    left:auto;
}
.toolButton{
    display: inline-block;
    position: relative;
    background-repeat: no-repeat;
    width: 40px;
    height: 40px;
    border: 1px solid #69abc3;
    border-radius: 5px;
    cursor:pointer;
}
.toolButtonUp {
    box-shadow: 0 0 0 60px rgba(0,0,0,0) inset, .1em .1em .2em #888;
    background: -webkit-gradient(linear, left top, left bottom, from(#c8e0e9), color-stop(25%, #FFFFFF), color-stop(70%, #c8e0e9), to(#9dc8d8));
    background: linear-gradient(#c8e0e9, #FFFFFF 25%, #c8e0e9 70%, #9dc8d8);
}
.toolButtonUp:hover{
    box-shadow: 0 0 0 60px rgba(0,0,0,0.1) inset, .1em .1em .2em #888;
}
.toolButtonDown {
    background: #4087a0;
    border-color: grey;
    top: .1em;
    left: .1em;
}

